.turn__container {
  position: relative;
  height: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 0;
}
.turn-counter__turn {
  font-size: 3em;
}

.turn-counter__counter {
  font-size: 3em;
  font-weight: 100;
  margin: 0.25em 0 0 0; 
}

.turn-counter__button {
  height: 20px;
  -ms-flex-item-align: center;
      align-self: center;
  margin: 0 10px;
}

.ring-visualiser {
  border-radius: 100%;
  background: rgba(200, 20, 5, 0.5);
  display: inline-block;
  position: absolute;
  top: 44px;
}

.ring-visualiser-will-be-destroyed {
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: blink;
          animation-name: blink;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes blink {
  0% {
    background: rgba(200, 20, 5, 0.5);
  }

  50% {
    background: rgba(150, 150, 150, 0.2);
  }

  100% {
    background: rgba(200, 20, 5, 0.5);
  }
}

@keyframes blink {
  0% {
    background: rgba(200, 20, 5, 0.5);
  }

  50% {
    background: rgba(150, 150, 150, 0.2);
  }

  100% {
    background: rgba(200, 20, 5, 0.5);
  }
}

.ring-visualiser-destroyed {
  background: rgba(150, 150, 150, 0.2);
}

.ring-visualiser-outer {
  width: 100px;
  height: 100px;
  margin-left: 75px;
}

.ring-visualiser-middle {
  width: 66px;
  height: 66px;
  position: relative;
  top: 16.66px;;
}

.ring-visualiser-inner {
  width: 33px;
  height: 33px;
  position: relative;
  top: 16.66px;
}

.turn-status__collapse {
  font-weight: 500;
  color: rgba(0,0,0,0.63);
  margin-top: 1em;
  margin-bottom: 0;
}


