.dice__container {
  height: 100%;
  -webkit-transition: all .25s cubic-bezier(.25,.8,.25,1);
  -o-transition: all .25s cubic-bezier(.25,.8,.25,1);
  transition: all .25s cubic-bezier(.25,.8,.25,1);
  opacity: 1;
}

.dice-counter__container {
  display: -ms-flexbox;
  display: flex;
}

.hide {
  opacity: 0;
  height: 0;
  overflow: hidden;
  margin: 0;
  padding: 0;
}


